@import url(https://fonts.googleapis.com/css?family=Oswald:wght@300;500&family=Petit+Formal+Script&family=Playfair+Display&display=swap);
body {
  margin: 0;
  font-family: 'Oswald', sans-serif;
  font-size: 1.2rem;
  font-weight: bold;
  text-transform: uppercase;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.App {
  min-height: 90vh;
  text-align: center;
}

.header {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #0F0066;
  font-size: 2rem;
  margin-top: 6rem;
}

.header span {
  font-family: 'Petit Formal Script', cursive;
  font-size: 6rem;
  text-transform: lowercase;
  color: #8670FF;
  margin-top: -15px;
}

.container-survey {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.wrapper-survey {
  margin-top: 30px;
}

.form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}
.form input {
  padding-left: 0;
  border-radius: 2px;
  padding: 5px;
  border: 1px solid gray;
}

.input {
  margin-bottom: 15px;
}

input[type="date"]
{
  font-family: 'Arial', sans-serif;
}


.buttonWrapper {
  text-align: center;
}

button {
  margin-top: 30px;
  width: 40%;
}

.wrapper-footer {
  margin-top: 60px;
  font-size: .9rem;
  color: #0F0066
}
